body{
    overflow: hidden;
    padding: 0;
    margin: 0;
}

.ace_editor{
    min-height: 40vh;
    max-height: 40vh;
    -ms-flex: 1 1;
        flex: 1 1;
}

.ace_print-margin{
    visibility: hidden !important;
}

textarea{
    resize: none;
    border-top: none;
    border-left: none;
    background-color: whitesmoke;
}

textarea:active, textarea:focus{
    outline: none;
}

textarea:last-child{
    border-right: none;
}

.top-container{
    min-height: 40vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.bottom-container{
    min-height: 60vh;
}

#frame{
    width: 100%;
    min-height: 60vh;
    border: none;
}

.CodeMirror.cm-s-default{
    min-height: 40vh;
    border-bottom: 1px solid #ddd;
}


